<template>
	  <div class="page has-navbar" v-nav="{'title': '评分详情'}">
    	<div class="page-content padding padding-top">

    	   <div  style="text-align:center "  >     
            <md-button  style="font-size:2em; width: 60%" class="button button-calm">√ 评分成功！</md-button >
           </div>  
    		
	  	   <div class="item item-divider">	  	  
		   	<textarea style="resize:none;" rows="10" cols="55" id="default-textarea" disabled>{{meetingmsg}}</textarea>
		   </div>
		   <br>
		   
		    <von-input 
			  
			  type="text" 
			  v-model="rater.score1" 
			  placeholder="维度一" 
			  label="维度一">
			</von-input>

		    <von-input 
			  type="text" 
			  v-model="rater.score2" 
			  placeholder="维度二" 
			  label="维度二">
			</von-input>

			<von-input 
			  type="text" 
			  v-model="rater.score3" 
			  placeholder="维度三" 
			  label="维度三">
			</von-input>

			<von-input 
			  type="text" 
			  v-model="rater.score4" 
			  placeholder="维度四" 
			  label="维度四">
			</von-input> 
		  
		</div>
	</div>
</template>

<script>
	export default {
		data() {
			return {
				meetingmsg:'心得心得心得心得心得心得心得心得心得心得心得心得心得心得心得心得心得心得心得心得心得心得心得心得心得心得心得心得心得心得心得心得心得心得心得心得',
				title:'评分详情',
				rater:{
					id:this.$route.query.raterId,
					score1:'',
					score2:'',
					score3:'',
					score4:'',
				}
			}
		},
		mounted(){
	        this.$http.get('https://open.auxgroup.com/api/auxhr/meetingrate/raters/'+this.rater.id).then(function(response) {
	        	this.meetingmsg = response.data[4];
	        	this.rater.score1 = response.data[0];
	        	this.rater.score2 = response.data[1];
	        	this.rater.score3 = response.data[2];
	        	this.rater.score4 = response.data[3];
	           console.log(response.data);
	        }, function(response) {
	            console.log(response);
	        });
  		},
  		
	}
</script>

<style>
	von-input{
		width:40px;
	}
</style>


